import React, {useState, useEffect, Fragment} from 'react';

const url = "https://raw.githubusercontent.com/Coding-Forest/2021-React/main/Mini%20Project%202%20Kiwi%20Bird%20Catalogue/bird.json";

const Catalogue = () => {
    const [birds, setBirds] = useState([]);

    const getBirds = async () => {
        const response = await fetch(url);
        const birds = await response.json();
        setBirds(birds);
    };

    useEffect(()=> {
        getBirds();
    }, []);

    return (
        <Fragment className='container'>
            <h1 className='main'>Friends in Birdieland</h1>
            <p className='intro'>Hello, welcome to Birdieland. Come meet the friends of Kiwi paradise!<br/>
                All bird images and data are from <a href="https://www.nzbirdsonline.org.nz/" target="_blank">New Zealand Birds Online</a></p>
            <ul className='birds'>
                {birds.map((bird)=> {
                    const { name, status, other_name, data_url, image_url
                     } = bird;
                    return (
                        <li key={name}>
                            <img src={image_url} alt={name} />
                            <div>
                                <p><a href={data_url}><h3>{name}</h3></a> <br/>
                                <strong>Status</strong>: {status}<br/>
                                <strong>Other names</strong>: {other_name}</p>
                            </div>
                        </li>
                    );
                })};
            </ul>
        </Fragment>

    )
}

export default Catalogue;

